.settings-page {
    max-width: 700px;
    margin: 0 auto;
    padding: 40px;
    font-family: 'Arial', sans-serif;
    color: #333;
  }
  
  h2 {
    text-align: center;
    margin-bottom: 30px;
    font-size: 2.5rem;
    color: #2c3e50;
  }
  
  .settings-section {
    background-color: #f9f9f9;
    padding: 20px;
    border-radius: 8px;
    margin-bottom: 20px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  }
  
  .settings-section h3 {
    margin-bottom: 15px;
    font-size: 1.5rem;
    color: #34495e;
  }
  
  label {
    display: block;
    margin-bottom: 10px;
    font-size: 1rem;
    color: #555;
  }
  
  input[type="text"],
  input[type="email"],
  input[type="password"] {
    width: 100%;
    padding: 10px;
    margin-top: 5px;
    border: 1px solid #ddd;
    border-radius: 4px;
    font-size: 1rem;
    margin-bottom: 15px;
  }
  
  input[type="checkbox"] {
    margin-right: 10px;
  }
  
  .save-button {
    display: block;
    width: 100%;
    padding: 12px;
    background-color: #27ae60;
    color: white;
    border: none;
    border-radius: 4px;
    font-size: 1.1rem;
    cursor: pointer;
    margin-top: 20px;
    transition: background-color 0.3s ease;
  }
  
  .save-button:hover {
    background-color: #1e8449;
  }
  