.job-listing-page {
    max-width: 800px;
    margin: 0 auto;
    padding: 20px;
    font-family: 'Arial', sans-serif;
  }
  
  h2 {
    text-align: center;
    margin-bottom: 20px;
    font-size: 2rem;
  }
  
  .filters {
    display: flex;
    justify-content: space-between;
    margin-bottom: 20px;
    gap: 10px;
  }
  
  .filters input[type="text"],
  .filters select {
    padding: 10px;
    width: calc(33.33% - 10px);
    border: 1px solid #ddd;
    border-radius: 4px;
    font-size: 1rem;
  }
  
  .job-listings {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
    gap: 20px;
  }
  
  .job-card {
    padding: 15px;
    border: 1px solid #ddd;
    border-radius: 8px;
    background-color: #f9f9f9;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
    transition: transform 0.2s;
  }
  
  .job-card:hover {
    transform: scale(1.02);
  }
  
  .job-card h3 {
    margin-top: 0;
    margin-bottom: 10px;
    font-size: 1.5rem;
  }
  
  .job-card p {
    margin: 5px 0;
    font-size: 0.9rem;
    color: #555;
  }
  
  .description {
    margin-bottom: 15px;
    font-size: 0.85rem;
  }
  
  .apply-button {
    padding: 10px 20px;
    background-color: #007bff;
    color: #fff;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    font-size: 1rem;
  }
  
  .apply-button:hover {
    background-color: #0056b3;
  }
  